.pagination {
    display: flex;
    width: 400px;
    align-items: center;
    justify-content: center;
    margin-bottom: 70px;
}

.pagination > button{

    width: 30px;
    font-size: 20px;
    border: none;
    background: none;
    cursor: pointer;
    margin: 0;
    padding: 0;
}